<template>
  <div class="train">
		<div class="title clearfix">
			<h3 class="fl"></h3>
			<!-- <div class="fr" :class="{currentActive : start && ('finish' != isfinish)  }" @click="goBack"><i class="fa fa-chevron-left"></i></div> -->
			<div class="fr" style="margin-top: 6px;">
				<goback :isGoBack="gobackFalg">
					<div class="fr" style=""><i class="fa fa-arrow-left"></i></div>
				</goback>
			</div>
		</div>
		<div class="train-main clearfix">
			<div class="train-main-top clearfix">
				<div>
					<el-button type="success"  @click="toStart" :disabled="start || !isOnline"><i class="fa fa-play"></i></el-button>
					<el-button type="success"  @click="toPause('finish')"  :disabled="topause" v-show="isPause"><i class="fa fa-pause"></i></el-button>
					<el-button type="success" @click="toPause('jixun')"  :disabled="!start" v-show="!isPause"><i class="fa fa-play"></i></el-button>
				</div>
				
			</div>
			<div class="train-main-middle">
				<el-steps :space="100" direction="vertical" :active="step">
				  <el-step title=" 1" description=""></el-step>
				  <el-step title=" 2" description=""></el-step>
				</el-steps>
			</div>
		</div>
		<div class="button">
			<div class="fl">
				<el-button type="info" :disabled="'finish' != isfinish" @click="angin">
					<i class="fa fa-repeat"></i>
					
				</el-button>
			</div>
			<div class="fr">
				<el-button type="success" class="btnd" @click="goNext" :disabled="'finish' != isfinish">&nbsp;&nbsp;&nbsp;&nbsp;<i class="fa fa-chevron-right"></i></el-button>
			</div>
		</div>
		<!-- loading -->
		<div>
			<StartTips :loadOver="loadOver"></StartTips>
		</div>
  </div>
</template>
<script>
import util from'../../../common/util'
import Goback from '../commonvue/backup'
import StartTips from '../commonvue/startprepare'
export default {
	components:{
		Goback,
		StartTips
	},
 	data(){
 		return {
 			loadOver: false,
 			hz:'',
 			current:'',
 			currentgame:'',
 			currentsign:'',
 			// 
 			start:false,
 			// 
 			isOnline : false,
 			// 
 			isfinish:'test',
 			isPause:true,
 			topause:true,
 			wsData :'',
 			step:0,
 			// 
 			successTimesNum:0,
 			// 
 			successAllNum:0,
 			// 
 			successResponseTime:0
 			// 
 			
 		}
 	},
 	// props:{
 	// 	socket:null,
 	// },
 	mounted(){
 		// 
 		this.loadOver = true;
 		// 
 		this.db= this.$route.query.crtdb;
 		this.hz = this.$route.query.data;
 		this.current = this.$route.query.isEar;
 		this.currentgame = this.$route.query.crtgame;
 		this.currentsign = this.$route.query.crtsign
 		// websocket
 		var that = this;
 		Vm.$on('wsmsg',(msg)=>{
 			that.wsData = msg;
 		})
 		Vm.$on('servermsg',(msg,res,str)=>{
 			// console.log(msg.mescon,res,str)
 			if(!that.closeLay){
	 			util.ctld_tran_isOnline(that,res,str);
	 		}
 		});
 	},
 	watch:{
		wsData:function(){
			// 
			if(this.isOnline){
				this.loadOver = false;
			};
			console.log(this.wsData.params)
			// 
			this.stops();
			// 
			this.successNum();
		}
 	},
 	computed:{
 		gobackFalg:function(){
 			let backfalg = this.start && ('finish' != this.isfinish)
 			return backfalg;
 		},
 		averageTime :function(){
 			let averageTime = Math.round(this.successResponseTime/this.successAllNum);
 			if(averageTime > 5 || isNaN(averageTime)){return averageTime = 5};
 			if(averageTime < 1){return averageTime = 1};
 			return  averageTime;
 		}
 	},
 	beforeRouteLeave(to,form,next){
 		// console.log(to,form,next);
 		this.closeLay = true;
 		next();
 	},
 	methods:{
 		// 
 		toStart(){
				// 
			var params = {
				'ear':'A',//
	    		'tonetype':this.currentsign,//
		    	'testType':'zhuting',//
		    	'game':this.currentgame,//
		    	'time':'5',//
		    	'hz': '500',//
		    	'level':this.$route.query['level'],
		    	'db':'50'//this.currentDb
			}
			// console.log(params)
	    	var argument = this.wskt.wstoctld('games_train_start',params);
	    	websocket.send(JSON.stringify(argument));
 			this.start = !this.start;
 			this.step = 1;
 			this.topause = false;
 		},
 		successNum(){
 			if(this.step === 2){
				if(this.wsData.params['success'] == 'true'){
					this.successTimesNum++;//
					this.successAllNum++;//
					// 
					this.successResponseTime += +this.wsData.params['feedbackTime'].toFixed(2);
					if(this.successTimesNum === 3){//
						this._trainOverTips();
					}
					return;
				}else{
					this.successTimesNum = 0;
				}
			}
 		},
 		// 
 		angin(){
 			util.angin(this);
 		},
 		// 
 		toPause(str){
 			util.toPause(str,this);
 		},
 		stops(){
 			util.stops(this);
 		},
 		// 
 		_trainOverTips(){
 			util._trainOverTips(this);
 		},
 		goNext(){
 			window.isToggle = false;
 			var param = this.hz;
 			var ear = this.current;
 			var games = this.currentgame;
 			var sign = this.currentsign;
 			websocket.send(JSON.stringify(this.wskt.goaudioplan('zhuting','official',games)));
 			util._setVolumNum(this,{'decibel':this.averageTime,'test_id':sessionStorage.getItem('test_id')});
	    	this.$router.push({ path: '/home/zhuting/audiometry',query:{data:param,crtdb:this.db,isEar:ear,crtgame:games,crtsign:sign,'level':this.$route.query['level']}})
 		}
 	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang ="less">
	
</style>



